<template>
    <div>
        <div class="loginNavFix">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">编辑作业</p>
        </div>
        <div class="setBox">
            <div class="setTitle pl-30 f28 c333">
                标题
                <input type="text" class="oinp" placeholder="请输入">
            </div>
            <div class="home-comntent">
                <p class="pl-30 f28 c333 pt-20 pb-30">内容</p>
                <textarea name="" id="" cols="30" class="textarea f26 pl-30 pr-30 pt-10" rows="10" placeholder="请输入帖子内容"></textarea>
            </div>
            <div class="upload">
                <p class="pl-30 f28 c333 pt-20 pb-30">图片</p>
                <div class="iploadBox">
                    <div class="imgShowBox">
                        <div class="imgShow">
                            <img src="../../../assets/login/erha.jpg" alt="">
                            <span @click="deleteThis" class="closeX f20">X</span>
                        </div>
                    </div>
                    <div class="imgShowBox">
                        <div class="imgShow">
                            <img src="../../../assets/login/erha.jpg" alt="">
                            <span @click="deleteThis" class="closeX f20">X</span>
                        </div>
                    </div>
                    <div class="imgShowBox">
                        <div class="imgShow">
                            <img src="../../../assets/login/erha.jpg" alt="">
                            <span @click="deleteThis" class="closeX f20">X</span>
                        </div>
                    </div>
                    <div class="imgShowBox">
                        <div class="imgShow">
                            <img src="../../../assets/login/erha.jpg" alt="">
                            <span @click="deleteThis" class="closeX f20">X</span>
                        </div>
                    </div>
                    <div class="imgShowBox">
                        <img class="imgShow" src="../../../assets/my/upload.png" alt="">
                    </div>
                </div>
            </div>
            <div class="upload mt-30">
                <p class="pl-30 f28 c333 pt-20 pb-30">视频</p>
                <div class="iploadBox">
                    <div class="imgShowBox">
                        <div class="imgShow">
                            <img src="../../../assets/login/erha.jpg" alt="">
                            <span @click="deleteThis" class="closeX f20">X</span>
                        </div>
                    </div>
                    <div class="imgShowBox">
                        <div class="imgShow">
                            <img src="../../../assets/login/erha.jpg" alt="">
                            <span @click="deleteThis" class="closeX f20">X</span>
                        </div>
                    </div>
                    <div class="imgShowBox">
                        <div class="imgShow">
                            <img src="../../../assets/login/erha.jpg" alt="">
                            <span @click="deleteThis" class="closeX f20">X</span>
                        </div>
                    </div>
                    <div class="imgShowBox">
                        <div class="imgShow">
                            <img src="../../../assets/login/erha.jpg" alt="">
                            <span @click="deleteThis" class="closeX f20">X</span>
                        </div>
                    </div>
                    <div class="imgShowBox">
                        <img class="imgShow" src="../../../assets/my/upload.png" alt="">
                    </div>
                </div>
            </div>
            <div class="setTitle pl-30 f28 c333 mt-30 borderTop" @click="modelShow=true">
                {{chooseClass}}
                <img src="" alt="">
                <img class="msgTo" src="../../../assets/my/more.png" alt="">
            </div>
            <div class="setTitle pl-30 f28 c333"  @click="modelShow1=true">
                {{chooseSubject}}
                <img class="msgTo" src="../../../assets/my/more.png" alt="">
            </div>
            <!-- <div style="height:1.8rem;"></div> -->
            <div class="centerBtn f26 cfff text-center" @click="next">确认修改</div>
        </div>
        <van-popup v-model="modelShow" position="bottom" :overlay="true" :closeOnClickOverlay="false">
            <van-picker
            show-toolbar
            :columns="columns"
            @cancel="onCancel"
            @confirm="onConfirm"
            />
        </van-popup>
        <van-popup v-model="modelShow1" position="bottom" :overlay="true" :closeOnClickOverlay="false">
            <van-picker
            show-toolbar
            :columns="columnClass"
            @cancel="onCancel1"
            @confirm="onConfirm1"
            />
        </van-popup>
    </div>
</template>

<script>
export default {
  name: 'HelloWorld',
    data () {
        return {
            chooseClass:'选择班级',
            chooseSubject:'选择科目',
            modelShow:false,
            modelShow1:false,
            columns: ['全部科目', '化学', '英语', '语文', '数学', '美术'],
            columnClass: ['选择科目', '2222', '英语', '语文', '数学', '美术'],
        }
    },
    methods : {
       returns(){
            this.$router.push({path:'/homeworkdetail',query:{id:this.$route.query.id}});//返回上一层
        },
        deleteThis(){
            alert('删除')
        },
        onConfirm(value, index) {
            this.$toast(`当前值：${value}, 当前索引：${index}`);
            this.modelShow = !this.modelShow;
            this.chooseClass = value;
        },
        onCancel(picker, value, index) {    
            this.modelShow = !this.modelShow;
        },
        onConfirm1(value, index) {
            this.$toast(`当前值：${value}, 当前索引：${index}`);
            this.modelShow1 = !this.modelShow1;
            this.chooseSubject = value;
        },
        onCancel1(picker, value, index) {    
            this.modelShow1 = !this.modelShow1;
        },
        next(){//跳转到下一个页面
            this.$router.push({path:'/homeworkdetail'})
        }
    },
    created() {
        console.log(this.$route.query.id)
    },
}

</script>

<style scoped>
.setBox{position: absolute;top: 0.84rem;left: 0;right: 0;bottom: 0;overflow-y: auto;}
.setTitle{height: 1.11rem;border-bottom: 1px solid #f2f2f2;line-height: 1.11rem;overflow: hidden;}
.oinp{float: right;border: 0;outline: 0;width: 80%;text-align: right;padding-right: 0.2rem}
.home-comntent{height: 3.74rem;border-bottom: 1px solid #f2f2f2;}
.textarea{width: 100%;height: 1.1rem;outline: 0;border: 0;box-sizing: border-box;resize:none;}
.iploadBox{margin-left: 0.3rem;overflow: hidden;}
.imgShowBox{width: 20%;float: left;}
.imgShow{width: 1.2rem;height: 1.2rem;border-radius: 5px;overflow: hidden;float: left;position: relative;}
.imgShow img{width: 100%;height: 100%;display: block;}
.closeX{position: absolute;top: 0;right: 0;padding: 0.05rem 0.1rem;}
.borderTop{border-top: 1px solid #f2f2f2;}
.msgTo{width: 0.3rem;height: 0.3rem;float: right;margin: 0.35rem 0.3rem 0 0;}
.centerBtn{height: 0.9rem;width: 6.9rem;border-radius: 0.9rem; background: linear-gradient(to bottom right, #82cbff , #7b7ceb);line-height: 0.9rem; box-shadow: 0 0 10px #f0f9ff; margin: 0.2rem auto; }

</style>





